<template>
    <div>
        <Toolbar>
            <template v-slot:title>
              Friends
            </template>
            <template v-slot:prepend>
                <v-btn icon @click="$router.go(-1)">
                    <v-icon>mdi-arrow-left</v-icon>
                </v-btn>
            </template>
            <template v-slot:append>
                <v-spacer></v-spacer>
                <v-btn icon @click="">
                    <v-icon>mdi-magnify</v-icon>
                </v-btn>
            </template>
        </Toolbar>
        <v-list v-if="friends && friends.length" class="pt-0">
          <template v-for="(item, index) in friends">
            <v-divider v-if="index != 0" :key="index" inset></v-divider>
            <v-list-item
                class="pr-1"
                :key="item.id"
            >
                <v-list-item-avatar>
                  <v-icon large>mdi-account-circle</v-icon>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title v-text="item.username"></v-list-item-title>
                </v-list-item-content>
                <v-list-item-action>
                    <v-btn small text @click.stop="deleteFriend(item.id, index)">
                        delete
                        <v-icon right color="error">mdi-trash-can-outline</v-icon>
                    </v-btn>
                </v-list-item-action>
            </v-list-item>
          </template>
          <v-divider inset></v-divider>
        </v-list>
        <template v-else>
            <p class="text-center pt-4 subtitle-1">
              You dont`t have friends.<br/>
              Add friends first on <router-link :to="{name: 'users'}">users</router-link> page.
            </p>
        </template>
    </div>
</template>

<script>
import Toolbar from '@/components/Toolbar'

export default {
  name: 'Friends',
  components: {
    Toolbar
  },
  data () {
    return {
    }
  },
  computed: {
    friends () {
      return this.$store.state.user.friends
    }
  },
  methods: {
    deleteFriend (id, index) {
      this.$store.dispatch('deleteFriend', id).then(resp => {
        this.friends.splice(index, 1)
        console.log(resp)
        //this.$forceUpdate()
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    this.$store.dispatch('getFriends')
  }
}
</script>
